.features {
  .container ();
  display: flex;
  flex-direction: column;

  padding-left: 19px;
}

.features__title-wrapper {
  margin-top: 16px;
  margin-bottom: 0;
}

.features__title {
  color: @light-black;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  text-transform: none;
}

.features__list {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
}

.features__item {
  margin-bottom: 26px;
  padding-left: 82px;
}

.features__item:nth-child(4) {
  margin-bottom: 0;
}

.features__description {
  position: relative;

  width: 200px;
  color: @grey-dark;
  font-family: @font-family-secondary;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-transform: none;
}

.features__description::before {
  content: "";

  position: absolute;
  top: -2px;
  left: -80px;

  width: 60px;
  height: 60px;

  background-color: @green;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.features__description--leaf {
  position: relative;

  &::after {
    content: "";

    position: absolute;
    top: 3px;
    left: -75px;

    width: 60px;
    height: 60px;

    background-image: url("../img/advantage-leaf@1x.png");
    background-repeat: no-repeat;
    background-position: 0 0;

    z-index: 2;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-leaf@2x.png");
      background-size: 50px auto;
    }
  }
}

.features__description--powder {
  position: relative;

  &::after {
    content: "";

    position: absolute;

    top: 3px;
    left: -75px;

    width: 60px;
    height: 60px;

    background-image: url("../img/advantage-powder@1x.png");
    background-repeat: no-repeat;
    background-position: 0 0;

    z-index: 2;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-powder@2x.png");
      background-size: 50px auto;
    }
  }
}

.features__description--dinin {
  position: relative;

  &::after {
    content: "";

    position: absolute;

    top: 3px;
    left: -75px;

    width: 60px;
    height: 60px;

    background-image: url("../img/advantage-dining@1x.png");
    background-repeat: no-repeat;
    background-position: 0 0;

    z-index: 10;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-dining@2x.png");
      background-size: 50px auto;
    }
  }
}

.features__description--clock {
  position: relative;

  &::after {
    content: "";

    position: absolute;
    top: 3px;
    left: -75px;

    width: 60px;
    height: 60px;

    background-image: url("../img/advantage-clock@1x.png");
    background-repeat: no-repeat;
    background-position: 0 0;

    z-index: 2;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-clock@2x.png");
      background-size: 50px auto;
    }
  }
}

@media (min-width: @tablet-width) {
  .features {
    padding-left: 0;
  }

  .features__title-wrapper {
    margin-top: 45px;
    margin-bottom: 0;
    margin-left: 30px;
  }

  .features__title {
    font-size: 60px;
    line-height: 60px;
    text-transform: none;
  }

  .features__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    padding-left: 30px;
  }

  .features__description {
    width: 250px;
    font-size: 16px;
    line-height: 24px;
  }

  .features__description::before {
    top: -113px;
    left: 1px;

    width: 80px;
    height: 80px;
  }

  .features__item {
    width: 201px;

    margin-top: 153px;
    margin-bottom: 60px;
    margin-left: 0;

    padding-right: 383px;
    padding-left: 0;
  }

  .features__item--one {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -96px;
      left: 126px;

      width: 71px;
      height: 228px;

      background-image: url("../img/1-tablet.png");
      background-repeat: no-repeat;
      background-position: 0 0;

      z-index: -1;
    }
  }

  .features__item--two {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -98px;
      left: 123px;

      width: 114px;
      height: 230px;

      background-image: url("../img/2-tablet.png");
      background-repeat: no-repeat;
      background-position: 0 0;

      z-index: -1;
    }
  }

  .features__item--three {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -98px;
      left: 123px;

      width: 113px;
      height: 250px;

      background-image: url("../img/3-tablet.png");
      background-repeat: no-repeat;
      background-position: 0 0;

      z-index: -1;
    }
  }

  .features__item--four {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -98px;
      left: 123px;

      width: 121px;
      height: 228px;

      background-image: url("../img/4-tablet.png");
      background-repeat: no-repeat;
      background-position: 0 0;

      z-index: -1;
    }
  }

  .features__item:nth-child(3n),
  .features__item:nth-child(4n) {
    margin-bottom: 0;
  }

  .features__item:nth-child(2),
  .features__item:nth-child(4) {
    padding-right: 0;
  }

  .features__description--leaf {
    position: relative;

    &::after {
      content: "";

      position: absolute;
      top: -97px;
      left: 15px;
    }
  }

  .features__description--powder {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -97px;
      left: 15px;
    }
  }

  .features__description--dinin {
    position: relative;

    &::after {
      content: "";

      position: absolute;

      top: -97px;
      left: 15px;
    }
  }

  .features__description--clock {
    position: relative;

    &::after {
      content: "";

      position: absolute;
      top: -97px;
      left: 15px;
    }
  }
}

