  }

  .features__wrapper {
    .container ();
  }

  .features__title-wrapper {
    margin-top: 73px;
    margin-bottom: 0;
    margin-left: 45px;

    width: 380px;
  }

  .features__list {
    padding-left: 45px;
  }

  .features__description::before {
    left: 0;
  }

  .features__item {
